const UPPER_CASE_RANGE_START = 'A'.charCodeAt(0);
const UPPER_CASE_RANGE_END = 'Z'.charCodeAt(0);

const LOWER_CASE_RANGE_START = 'a'.charCodeAt(0);
const LOWER_CASE_RANGE_END = 'z'.charCodeAt(0);

const NUMBER_RANGE_START = '0'.charCodeAt(0);
const NUMBER_RANGE_END = '9'.charCodeAt(0);

