import React, {
  FC,
  useState,
  useCallback,
  ChangeEvent,
  MouseEvent,
} from 'react';
import classNames from 'classnames';

import { Card } from 'components/card';

import styles from './Home.module.css';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';

export const Home: FC = () => {
  let [text, setText] = useState('');

  let onTextAreaChange = useCallback(
    ({ currentTarget: { value } }: ChangeEvent<HTMLTextAreaElement>) => {
      setText(value);
    },
    [],
  );

  let onLinkClick = useCallback(
    (event: MouseEvent) => {
      if (!text) {
        event.preventDefault();
      }
    },
    [text],
  );

  return (
    <div className={styles.home}>
      <Card className={classNames(styles.mainCard)}>
        <div className={styles.logo}>ùíÆùìéùìÇùí∑‚Ñ¥ùìÅùìà</div>
        <textarea
          className={styles.mainCardTextArea}
          value={text}
          onChange={onTextAreaChange}
          placeholder="Input your text here..."
        />
      </Card>
      <Link
        className={styles.transformLink}
        onClick={onLinkClick}
        to={`/select-mapper/${encodeURIComponent(text)}`}
      >
        <Button className={styles.transformButton}>ùïãùï£ùïíùïüùï§ùïóùï†ùï£ùïû ùï•ùï† ùï§ùï™ùïûùïìùï†ùïùùï§</Button>
      </Link>
    </div>
  );
};
